* {
  box-sizing: border-box;
}

body {
  margin: 0;
}

.container {
  margin: 1rem;
  text-align: center;
}

.nav {
  background-color: #555;
  color: white;
  display: flex;
  justify-content:space-around;
  align-items: stretch;
  grid-gap: 2rem;
  gap: 2rem;
  padding: 2rem 2rem;
}

.site-title {
  font-size: 2rem;
  text-align: center;
  padding: 5rem;
}

.nav ul {
  padding: .5rem;
  margin: 0;
  list-style: none;
  display: flex;
  grid-gap: 1rem;
  gap: 1rem;
}

.nav a {
  color: inherit;
  -webkit-text-decoration: silver;
          text-decoration: silver;
  height: 100%;
  display: flex;
  align-items: center;
  padding: .25rem;
}

.nav li.active {
  background-color: green;
}

.nav li:hover {
  background-color: #777;
}

.membck {
  background-image: -webkit-linear-gradient(rgb(243, 236, 236),rgb(249, 249, 249));
}

.App {
  text-align: center;
}

.bck {
  background-image: -webkit-linear-gradient(rgb(213, 46, 46),lightgreen,green);
  }

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.bold {
font-weight: bolder;
}
